.BookListItem {
    display: flex;
    margin: 15px 0;
  }
  
  .BookListItem .BookCover {
    flex-shrink: 0;
    width: 120px;
    margin-right: 30px;
  }
  
  .BookListItem .BookCover img {
    max-width: 100%;
  }
  
  .BookListItem .BookDetails {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 10px;
    align-items: flex-start;
  }
  
  .BookListItem .BookDetails .BookTitle {
    font-size: 36px;
    font-weight: 500
  }
  
  .BookListItem .BookDetails .BookPrice {
    font-size: 1.4rem;
  
  }
  